
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  margin-top: 60px;
}

.calculator[data-v-545cd71f] {
  font-family: "Roboto Mono", monospace;
  width: 700px;
  margin: 100px auto;
  font-size: 36px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(50px, auto);
  background-color: rgb(226, 223, 223);
}
.calculator div[data-v-545cd71f] {
  border: 3px solid #dcd0c0;
  padding-top: 20px;
  padding-bottom: 20px;
}
.disp[data-v-545cd71f] {
  grid-column: 1/5;
  color: white;
  background-color: #373737;
}
.zero[data-v-545cd71f] {
  grid-column: 1/3;
}
.btn[data-v-545cd71f] {
  background-color: rgb(128, 121, 111);
  color: white;
}
.btn[data-v-545cd71f]:hover {
  background-color: rgb(105, 102, 98);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.operator[data-v-545cd71f] {
  background-color: rgb(151, 122, 85);
}
.operator[data-v-545cd71f]:hover {
  background-color: rgb(126, 98, 58);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.grid-header {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 10vw;
}
h1 {
  padding-top: 7vh;
  grid-column: 2/4;
  font-size: 39px;
  color:#C0B283;
}
